import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import axios from "axios";

function CandlestickChart(props) {
  const [candlestickData, setCandlestickData] = useState(null);

  const fetchCandlestickData = (symbol) => {
    const baseUrl = "https://api.binance.com";
    const endPoint = "/api/v3/klines";
    const interval = "1w"; //  
    const currentTime = new Date().getTime(); //    
    const startTime = currentTime - 50 * 7 * 24 * 60 * 60 * 1000; //   (50  )
    const endTime = currentTime; //  

    axios
      .get(
        `${baseUrl}${endPoint}?symbol=${symbol}&interval=${interval}&startTime=${startTime}&endTime=${endTime}`
      )
      .then((response) => {
        //    
        const responseData = response.data;
        console.log(responseData);
        //    ,     
        const formattedData = responseData.map((item) => ({
          x: new Date(item[0]),
          y: [
            parseFloat(item[1]),
            parseFloat(item[2]),
            parseFloat(item[3]),
            parseFloat(item[4]),
          ],
          volume: parseFloat(item[5]), //     
        }));

        //     
        console.log(formattedData);
        setCandlestickData(formattedData);
      })
      .catch((error) => {
        console.error("   GET-:", error);
      });
  };

  useEffect(() => {
    //   fetchCandlestickData    symbol  props
    fetchCandlestickData(props.symbol);
  }, [props.symbol]);

  const options = {
    title: {
      text: props.symbol,
      align: "left",
    },
    xaxis: {
      type: "datetime",
    },

    yaxis: [
      {
        tooltip: {
          enabled: true,
        },
        labels: {
          formatter: function (value) {
            return value ? value.toFixed(4) + " $" : 0;
          },
        },
      },
      {
        seriesName: "Volume",
        opposite: true,
        title: {
          text: "Volume",
        },
        labels: {
          formatter: function (value) {
            return value / 1000000 + " m";
          },
        },
      },
    ],
  };

  return (
    <div style={{ background: "#ffffff" }}>
      {candlestickData && (
        <Chart
          type="candlestick"
          height={400}
          options={options}
          series={[
            { name: "price", type: "candlestick", data: candlestickData },
            {
              name: "Volume",
              type: "column",

              data: candlestickData.map((item) => ({
                x: item.x,
                y: item.volume,
              })),
            },
          ]}
        />
      )}
    </div>
  );
}

export default CandlestickChart;
